import React from "react";
import "./styles.scss";
import t from "./language";

class FinishPage extends React.Component {

    onChange = e => {
        this.props.onChange({
            completed: true,
            result: {
                value: e.target.value
            }
        })
    };

    render() {
        return <div className="finish-page">
            <div className="title">{t.title}</div>
            <div className={"picture"}><Picture/></div>
        </div>
    }
}

export default FinishPage;



const Picture = () => {
    return <svg width="400" height="288" viewBox="0 0 400 288" fill="none">
            <rect width="400" height="287.535" fill="url(#pattern0)"/>
            <defs>
                <pattern id="pattern0" patternContentUnits="objectBoundingBox" width="1" height="1">
                    <use href="#image0" transform="scale(0.000692521 0.000963391)"/>
                </pattern>
                <image id="image0" width="1444" height="1038" href="data:image/jpeg;base64,"/>
            </defs>
        </svg>;
};